* {
    box-sizing: border-box; }
body,
h1,
h2,
h3,
h4,
h5 {}
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0; }

h1,
h2,
h3,
h4,
h5 {
    margin-block-start: 0;
    margin-block-end: 0; }

body {
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: 15px;
    line-height: 1.42857143;
    color: #333;
    background-color: #fff; }

.attention {
    text-align: left; }


header {
    text-align: center; }




.img {
    font-style: italic;
    text-align: center;
    margin: 40px 0 50px 0;
    p {
        margin-bottom: 0;
        margin-top: 30px; } }


.head {
    text-align: center;
    margin: 5% 0 2% 0; }


.center {
    text-align: center; }


.quote_from_diffrent_book {
    text-align: right;
    font-style: italic; }


.quote {
    border: 1px solid black;
    padding: 5px;
    font-style: italic;
    font-size: 15px;
    margin: 5% 0 7% 0; }


.book {
    margin: 10% 10% 10% 10%;
    max-width: 800px;
    margin: 0 auto;
    p {
        text-indent: 2%; } }





table {
    padding: 20px;
    margin: 20px;
    font-size: 15px;
    border-spacing: 1px 15px solid grey;
    vertical-align: middle;
    border: 1px solid black;
    border-collapse: collapse;
    td {
        white-space: normal;
        word-wrap: break-word;
        border: 1px solid black;
        padding: 10px; }
    th {
        border-bottom: 1px solid grey; } }






.quotefrombook {
    background-color: #ece8e8;
    margin-bottom: 17 px 20px 20px 0; }

h1 {
    font-size: 42px;
    padding-bottom: 50px; }
h2 {
    font-size: 30px;
    padding: 46px 0 42px; }
h3 {
    font-size: 22px;
    padding: 30px 0 27px; }

h4 {
    font-size: 19px;
    font-weight: bold;
    padding: 25px 0 22px; }

h5 {
    font-size: 16px;
    padding: 20px 0 18px; }




.link_to_pspo {
    font-size: 20px;
    font-style: bold;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }

a:hover {
    color: blue; }

@media (max-width: 1199px) {
    .book {
        width: 960px; } }

footer {
    margin: 10px 70px 100px; }

.link {
    align-items: left; }
